.card {
  width: 280px;
  display: block;
  margin: 60px auto;
  box-shadow: 10px 5px 40px 20px darken(#341cac, 5%);
  transition: 0.25s;
  min-height: 410px;
  &:hover {
    box-shadow: 10px 5px 40px 20px darken(#341cac, 10%);
    cursor: pointer;

    .button-primary {
      &:hover {
        cursor: pointer;
      }
      transform: translate(10px, 0);
    }
  }
  &__header {
    height: 150px;
    width: 100%;
    width: 100%;
    background-size: cover;
    color: #fff;
  }

  &__body {
    padding: 15px 15px 0;
    background-color: #fff;
    min-height: 280px;
  }

  &__date {
    font-size: 11px;
    font-weight: 600;
    color: grey;
  }
  &__content {
    padding-bottom: 40px;
    font-size: 13px;
    line-height: 1.8;
  }
  &__button-primary {
    border: none;
    box-shadow: none;
    font-family: inherit;
    background-color: transparent;
    color: blue;
    font-size: 15px;
    transition: 0.25s;
    padding: 15px 0;
    font-weight: 600;
    i {
      padding-right: 4px;
    }
  }
}
